import { createTableInstance } from '../helpers';
import normalizeColsWidth from '../../../bi/utils/normalizeColsWidth'
import { MIN_COL_WIDTH } from '../../../utils/constants';

describe('bi/table/utils/normalizeColsWidth', () => {
  it('invalid colsWidth', () => {
    const table = createTableInstance({
      table: [['A', 'B']],
      colsWidth: null,
    });
    const colsWidth = normalizeColsWidth(table);
    expect(colsWidth).toEqual([MIN_COL_WIDTH, MIN_COL_WIDTH]);
  });

  it('col width is not number', () => {
    const colsWidth = [null, 50];
    const table = createTableInstance({
      colsWidth,
      isAutofit: true,
    });
    expect(normalizeColsWidth(table)).toEqual([MIN_COL_WIDTH, 50]);
  });
});
